<template>
  <modal modalWidth="min(25em,90%)" :buttonColor="colorTheme">
    <template #content>
      <h3 style="text-align:center">Форма зворотного зв'язку</h3>
      <div class="modal-text">Надішліть питання за допомогою цієї форми, і ми відповімо вам протягом доби</div><br/>
      <input-text v-model='reportData.name' ref='inputName' placeholder="Ваше ім'я"/>
      <input-email v-model='reportData.email' ref='inputEmail'/>
      <input-textarea v-model='reportData.text' placeholder="Питання чи опис проблеми" ref='inputText'/>
    </template>
    <template #buttons>
      <button class="button-small" :style="{ background: colorTheme }" @click="report()">Надіслати</button>
    </template>
  </modal>
</template>

<script>
import controller from '@/api/controller'
import inputText from '@/components/input-text.vue'
import inputEmail from '@/components/input-email.vue'
import inputTextarea from '@/components/input-textarea.vue'
import modal from '@/components/modal-basic.vue'
export default {
  components: { inputText, inputEmail, inputTextarea, modal },
  data() { return {
    colorTheme: localStorage.getItem('colorTheme'),
    reportData: {
      name: '',
      email: '',
      text: ''
    },
  }},
  computed: {
    inputs() { return Object.keys(this.$refs).map(e => this.$refs[e]) },
  },
  methods: {
    async report() {
      this.inputs.map(e => e.v$.$touch())
      if (!this.inputs.some(e => e.v$.$error)) {
        await controller.report('Нове звернення на skywing.online', this.reportData)
        this.$emit("update:visible", false)
      }
    }
  }
}
</script>